/**
 * minê³¼ max ì‚¬ì´ì˜ ëœë¤ ì •ìˆ˜ ë°˜í™˜ (min, max í¬í•¨)
 */
export const randomInt = (min: number, max: number): number => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

/**
 * minê³¼ max ì‚¬ì´ì˜ ëœë¤ ì‹¤ìˆ˜ ë°˜í™˜
 */
export const randomFloat = (min: number, max: number): number => {
  return Math.random() * (max - min) + min;
};

/**
 * ë°°ì—´ì—ì„œ ëœë¤ ìš”ì†Œ ì„ íƒ
 */
export const randomElement = <T>(array: T[]): T | undefined => {
  if (array.length === 0) return undefined;
  return array[randomInt(0, array.length - 1)];
};

/**
 * ë°°ì—´ ì„ê¸° (Fisher-Yates ì•Œê³ ë¦¬ì¦˜)
 */
export const shuffleArray = <T>(array: T[]): T[] => {
  const shuffled = [...array];
  for (let i = shuffled.length - 1; i > 0; i--) {
    const j = randomInt(0, i);
    [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
  }
  return shuffled;
};

/**
 * ê°€ì¤‘ì¹˜ ê¸°ë°˜ ëœë¤ ì„ íƒ
 */
export const weightedRandom = <T>(
  items: T[],
  weights: number[]
): T | undefined => {
  if (items.length === 0 || items.length !== weights.length) {
    return undefined;
  }

  const totalWeight = weights.reduce((sum, weight) => sum + weight, 0);
  let random = Math.random() * totalWeight;

  for (let i = 0; i < items.length; i++) {
    random -= weights[i];
    if (random <= 0) {
      return items[i];
    }
  }

  return items[items.length - 1];
};

/**
 * í™•ë¥  ê¸°ë°˜ true/false ë°˜í™˜
 */
export const randomChance = (probability: number): boolean => {
  return Math.random() < probability;
};